import React, { Component,useState } from 'react';
import { View, StyleSheet, Button, ScrollView,ToastAndroid,TextInput } from 'react-native';

import * as ImagePicker from 'expo-image-picker';
import axios from 'axios';


export default function App(){
  let url="";
  const [image, setImage] = useState(null);
  const [title,setTitle]=useState("")
  const [description,setDescription]=useState("")
  const [inprice,setInprice]=useState("")
  const [outprice,setOutprice]=useState("")
  const [category,setCategory]=useState("")
  const [brand,setBrand]=useState("")




  const pickImage = async () => {
    let result = await ImagePicker.launchImageLibraryAsync({
      mediaTypes: ImagePicker.MediaTypeOptions.All,
      allowsEditing: true,
      quality: 1,
    });
  
    console.log(result.uri);
    
    
  
    if (!result.cancelled) {
      //setImage(result.uri);
      let localUri = result.uri;
  let filename = localUri.split('/').pop();

  // Infer the type of the image
  let match = /\.(\w+)$/.exec(filename);
  let type = match ? `image/${match[1]}` : `image`;
  console.log(match,type,filename)

  // Upload the image using the fetch and FormData APIs
  let formData = new FormData();
  // Assume "photo" is the name of the form field the server expects
  formData.append('photo', { uri: localUri, name: filename, type });


      axios.post("http://hiteshikaryanastore.esy.es/productadding/fileupload.php", formData, { 
        method:"post",
        mode: 'no-cors',
        headers: {
            'Content-Type': 'multipart/from-data',
  
           'Access-Control-Allow-Origin':'*'
          },
        body:formData // receive two parameter endpoint url ,form data 
  })
  .then(data=>{
 
  url=data.data.url;
  console.log(url)
  ToastAndroid.showWithGravity(
    "Uploaded",
    ToastAndroid.SHORT,
    ToastAndroid.CENTER
  );
  })
     
    }
  };


 const  handleSubmit = () => {

  const data=new FormData();
  data.append("title",title)
  data.append("description",description)
  data.append("inprice",inprice)
  data.append("outprice",outprice)
  data.append("category",category)
  data.append("brand",brand)
  data.append("url",url)
//   JSON.stringify({
//     title:title,
//     description:description,
//     inprice:inprice,
//     outprice:outprice,
//     category:category,
//     brand:brand,
//     url:url
// })

  
    axios.post("http://hiteshikaryanastore.esy.es/productadding/Uploadproduct.php",data,{
      method:"POST",
      headers: {
        'Content-Type': 'application/json',

       'Access-Control-Allow-Origin':'*'
      },
      body:data
    })
    .then(data=>{
      console.log(data.data)
    })
    console.log(JSON.stringify({
      title,
      description,
      inprice,
      outprice,
      category,
      brand,
      url
  }))
    
  }
  
  
  
    return (
      <ScrollView>

      <View style={styles.container}>
       

<View style={styles.button1}>
          <TextInput 
                placeholder="title" 
                placeholderTextColor="#e6e6e6" 
                style={styles.input}
                type="text"
                value={title}
                onChange={(e)=>setTitle(e.nativeEvent.text)} >

          </TextInput>

          <TextInput 
                placeholder="description" 
                placeholderTextColor="#e6e6e6" 
                style={styles.input}
                type="text"
                value={description}
                onChange={(e)=>setDescription(e.nativeEvent.text)} >

          </TextInput>

          <TextInput 
                placeholder="inprice" 
                placeholderTextColor="#e6e6e6" 
                style={styles.input}
                keyboardType = 'numeric'
                value={inprice}
                onChange={(e)=>setInprice(e.nativeEvent.text)} >

          </TextInput>

          <TextInput 
                placeholder="outprice" 
                placeholderTextColor="#e6e6e6" 
                style={styles.input}
                keyboardType = 'numeric'
                value={outprice}
                onChange={(e)=>setOutprice(e.nativeEvent.text)} >

          </TextInput>

          <TextInput 
                placeholder="category" 
                placeholderTextColor="#e6e6e6" 
                style={styles.input}
                type="text"
                value={category}
                onChange={(e)=>setCategory(e.nativeEvent.text)} >

          </TextInput>

          <TextInput 
                placeholder="brand" 
                placeholderTextColor="#e6e6e6" 
                style={styles.input}
                type="text"
                value={brand}
                onChange={(e)=>setBrand(e.nativeEvent.text)} >

          </TextInput>
        


<Button title="Pick an image from camera roll"  type="file" onPress={()=>pickImage()}  ></Button></View>

      {image && <Image source={{ uri: image }} style={{ width: 200, height: 200 }} />}
        <Button
          title="Add"
          onPress={()=>handleSubmit()}
        />
      </View>
      </ScrollView>

    );
  
}

const styles = StyleSheet.create({
  container: {
    justifyContent: 'center',
    marginTop: 50,
    padding: 20,
  },
  button1:{
    marginBottom:10
  },
  input:{
    margin:10,
    padding:10,
    backgroundColor:"#999999"
  }
});
